













import Vue from 'vue';

export default Vue.extend({
  name: 'VariableEditor',
  data () : { new_key : string | null } {
    return({ new_key: null })
  },
  props: {
    variables: Object,
  },
  methods: {
    add_variable () {
      if (this.new_key) {
        this.$emit('update', this.new_key, '123')
        this.new_key = null;
      }
    },
    update_variable (key : string, event : any) {
      this.$emit('update', key, event.target.value)
    },
  },
});
